'use client';

import { useState, useEffect } from 'react';
import {
  Layout,
  Card,
  Button,
  Space,
  Typography,
  Row,
  Col,
  Statistic,
  message,
  Breadcrumb,
  Table,
  Tag,
  Spin
} from 'antd';
import {
  TeamOutlined,
  PlusOutlined,
  SearchOutlined,
  UserOutlined,
  CheckCircleOutlined,
  ExclamationCircleOutlined,
  ClockCircleOutlined
} from '@ant-design/icons';
import Link from 'next/link';
import { ROUTES } from '@/lib/routes';
import dayjs from 'dayjs';

const { Header, Content } = Layout;
const { Title, Text } = Typography;

export default function ColaboradoresPage() {
  const [dashboardData, setDashboardData] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      console.log('Buscando dados do dashboard...');
      const response = await fetch('/api/dashboard');
      console.log('Response status:', response.status);
      
      if (response.ok) {
        const data = await response.json();
        console.log('Dados recebidos:', data);
        setDashboardData(data);
      } else {
        const errorText = await response.text();
        console.error('Erro na resposta:', errorText);
        setError(`Erro ${response.status}: ${errorText}`);
        message.error('Erro ao buscar dados do dashboard');
      }
    } catch (error) {
      console.error('Erro ao buscar dados:', error);
      setError(`Erro de conexão: ${error}`);
      message.error('Erro ao buscar dados');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div style={{ 
        display: 'flex', 
        justifyContent: 'center', 
        alignItems: 'center', 
        height: '100vh',
        flexDirection: 'column'
      }}>
        <Spin size="large" />
        <Text style={{ marginTop: 16 }}>Carregando dados...</Text>
      </div>
    );
  }

  if (error) {
    return (
      <div style={{ 
        display: 'flex', 
        justifyContent: 'center', 
        alignItems: 'center', 
        height: '100vh',
        flexDirection: 'column'
      }}>
        <Text type="danger">Erro: {error}</Text>
        <Button onClick={fetchDashboardData} style={{ marginTop: 16 }}>
          Tentar Novamente
        </Button>
      </div>
    );
  }

  return (
    <div style={{ padding: '24px' }}>
      <div style={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center',
        marginBottom: '24px'
      }}>
        <Title level={3} style={{ margin: 0, color: '#1890ff' }}>
          Gerenciamento de Colaboradores
        </Title>
        <Space>
          <Link href={ROUTES.COLABORADORES.CADASTRAR}>
            <Button type="primary" icon={<PlusOutlined />}>
              Novo Colaborador
            </Button>
          </Link>
          <Link href={ROUTES.DASHBOARD}>
            <Button>
              Voltar ao Dashboard
            </Button>
          </Link>
        </Space>
      </div>
      
      {/* Estatísticas */}
      <Row gutter={[16, 16]} style={{ marginBottom: 24 }}>
        <Col xs={24} sm={12} lg={6}>
          <Card>
            <Statistic
              title="Total de Colaboradores"
              value={dashboardData?.estatisticas.totalColaboradores || 0}
              prefix={<TeamOutlined />}
              valueStyle={{ color: '#1890ff' }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={12} lg={6}>
          <Card>
            <Statistic
              title="Colaboradores Ativos"
              value={dashboardData?.estatisticas.colaboradoresAtivos || 0}
              prefix={<CheckCircleOutlined />}
              valueStyle={{ color: '#52c41a' }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={12} lg={6}>
          <Card>
            <Statistic
              title="Colaboradores Inativos"
              value={dashboardData?.estatisticas.colaboradoresInativos || 0}
              prefix={<ExclamationCircleOutlined />}
              valueStyle={{ color: '#ff4d4f' }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={12} lg={6}>
          <Card>
            <Statistic
              title="Novos Cadastros"
              value={dashboardData?.estatisticas.novosCadastros || 0}
              prefix={<UserOutlined />}
              valueStyle={{ color: '#722ed1' }}
            />
          </Card>
        </Col>
      </Row>

      {/* Ações Rápidas */}
      <Row gutter={[16, 16]} style={{ marginBottom: 24 }}>
        <Col xs={24} lg={12}>
          <Card title="Ações Rápidas">
            <Space direction="vertical" style={{ width: '100%' }}>
              <Link href={ROUTES.COLABORADORES.CADASTRAR}>
                <Button type="primary" icon={<PlusOutlined />} block>
                  Cadastrar Novo Colaborador
                </Button>
              </Link>
              <Link href={ROUTES.COLABORADORES.LISTAR}>
                <Button icon={<SearchOutlined />} block>
                  Ver Todos os Colaboradores
                </Button>
              </Link>
              <Link href={ROUTES.FERIAS.LISTAR}>
                <Button icon={<ClockCircleOutlined />} block>
                  Gerenciar Férias
                </Button>
              </Link>
            </Space>
          </Card>
        </Col>
        <Col xs={24} lg={12}>
          <Card title="Colaboradores Recentes">
            <div>
              {dashboardData?.colaboradoresRecentes?.map((colaborador: any) => (
                <div key={colaborador.id} style={{ 
                  padding: '8px 0', 
                  borderBottom: '1px solid #f0f0f0',
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center'
                }}>
                  <div>
                    <Text strong>{colaborador.nome}</Text>
                    <br />
                    <Text type="secondary">{colaborador.cargo} - {colaborador.departamento}</Text>
                  </div>
                  <Tag color={colaborador.status === 'ativo' ? 'green' : 'red'}>
                    {colaborador.status === 'ativo' ? 'Ativo' : 'Inativo'}
                  </Tag>
                </div>
              ))}
            </div>
          </Card>
        </Col>
      </Row>
    </div>
  );
}
